  --text-color-secondary: #4a4c4e;
  --text-size-main: .95em;
  --text-size-secondary: .9em;
  --button-bg-color: #1a73e8;
  --button-bg-color--hov: #1b66c9;
  --button-border-color--hov: #d2d5d9;
  --button-text-color: #fff;
  --neon: 0 0 1px #d2d5d9cc, 0 0 4px #d2d5d9cc, 0 0 8px #d2d5d9cc;
  --border-color: #d2d5d9;
  --border-radius: 4px;
  --transition-duration: .3s;
}

[data-theme="oddity"] {
  --bg-color-main: #0f0f0f;
  --bg-color-secondary: #1c1c1c;
  --text-color-main: #fff;
  --text-color-secondary: #909090;
  --text-size-main: .95em;
  --text-size-secondary: .9em;
  --button-bg-color: #d3da54;
  --button-bg-color--hov: #b8c03a;
  --button-border-color--hov: #b8c03a;
  --button-text-color: #0f0f0f;
  --neon: 0 0 1px #5c611299, 0 0 4px #b8c03a99, 0 0 8px #b8c03a99;
  --border-color: #333;
  --border-radius: 4px;
  --transition-duration: .3s;
}

body, ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

li {
  display: inline-block;
}

input {
  all: unset;
}

button {
  background-color: #0000;
  border: none;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Roboto, Arial, sans-serif;
  font-size: var(--text-size-main);
  font-weight: 400;
}

a {
  cursor: pointer;
  transition-property: background-color, box-shadow;
  transition-duration: var(--transition-duration);
  display: inline-block;
}

button {
  cursor: pointer;
}

.page {
  background-color: var(--bg-color-main);
  grid-template-rows: auto 1fr auto;
  width: 100vw;
  min-height: 100vh;
  display: grid;
}

.logo {
  width: 30%;
  height: 132px;
  margin-bottom: 20px;
  position: relative;
}

.logo__image {
  object-fit: contain;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.logo__image--hidden {
  opacity: 0;
  transition-property: opacity, transform;
  transition-duration: var(--transition-duration);
  transform: translateX(-100%);
}

#logo__image--google {
  width: 70%;
  left: 15%;
}

#logo__image--oddity {
  top: -20px;
}

.header {
  flex-direction: row-reverse;
  padding: 8px;
  display: flex;
}

.nav__list {
  align-items: center;
  gap: 16px;
  display: flex;
}

.nav__link {
  color: var(--text-color-main);
  font-size: var(--text-size-secondary);
}

.nav__link:after {
  content: "";
  transition: border-color var(--transition-duration);
  border-bottom: 1px solid #0000;
  width: 100%;
  margin-top: 1px;
  display: block;
}

.nav__link:hover {
  color: var(--text-color-main);
}

.nav__link:hover:after {
  border-color: var(--text-color-main);
}

.nav__services-button {
  border-radius: 50%;
  place-content: center;
  place-items: center;
  width: 36px;
  height: 36px;
  padding: 6px;
  display: flex;
}

.nav__services-button:hover {
  background-color: var(--bg-color-secondary);
}

.nav__services-image {
  fill: var(--text-color-main);
}

.nav__asign-button {
  color: var(--button-text-color);
  background-color: var(--button-bg-color);
  border-radius: var(--border-radius);
  box-shadow: none;
  margin: 0 8px;
  padding: 8px 24px;
}

.nav__asign-button:hover {
  background-color: var(--button-bg-color--hov);
  box-shadow: 0 1px 3px 1px #00000026, 0 1px 2px #0000004d;
}

.main {
  flex-direction: column;
  place-items: center;
  padding-top: 8%;
  display: flex;
}

.main__footer {
  color: var(--text-color-main);
  font-size: var(--text-size-secondary);
}

.main__language {
  color: var(--button-bg-color);
}

.searching {
  width: 40%;
  margin-bottom: 20px;
}

.searching__area {
  border: 1px solid var(--border-color);
  transition: box-shadow var(--transition-duration);
  border-radius: 32px;
  gap: 16px;
  width: 100%;
  margin-bottom: 20px;
  padding: 0 16px;
  display: flex;
}

.searching__area:hover {
  box-shadow: var(--neon);
}

.searching__field {
  color: var(--text-color-main);
  flex-grow: 1;
}

.searching__buttons {
  justify-content: center;
  display: flex;
}

.searching__button {
  color: var(--text-color-secondary);
